div#movieList {
  width: 100%;
  height: 100%;
}

ol, div#movieList ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

div#movieList ul {
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  margin: 0;
  padding: 0;
  display: flex;
}

div#movieList li {
  width: 370px;
  width: 280px;
  background-color: $ secondary-text-color;
  transition: box-shadow 450ms$ timing-function;
  border-radius: 5px;
  align-items: center;
  margin: 0 auto 20px;
  padding: 0 0 10px;
  display: list-item;
}

div#movieList li:hover {
  box-shadow: $ cards-shadows-box$ modal-topic-color;
  cursor: pointer;
}

div#movieList img {
  width: 280px;
  height: 398px;
  object-fit: cover;
  border-radius: 5px;
  margin-bottom: 10px;
}

div#movieList h2 {
  text-transform: uppercase;
  letter-spacing: 1.5px;
  margin-top: 0;
  margin-bottom: 0;
  margin-left: 11px;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-style: normal;
}

div#movieList span1 {
  color: #ff6b08;
  flex-wrap: wrap;
  margin-left: 11px;
  padding-top: 0;
  padding-right: 3px;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-style: normal;
  line-height: 1.1;
  display: inline-flex;
}

div#movieList span2 {
  letter-spacing: normal;
  width: 50px;
  height: 16px;
  color: #fff;
  background-color: #ff6b01;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  padding-top: 1px;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
  line-height: 1.1;
}

div#movieList span3 {
  color: #ff6b08;
  flex-wrap: wrap;
  margin-left: 5px;
  padding-top: 0;
  padding-right: 3px;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-style: normal;
  line-height: 1.1;
  display: inline-flex;
}

body {
  margin: 20px;
  font-family: Arial, sans-serif;
}

.search-movie {
  margin-bottom: 20px;
}

form {
  display: flex;
}

input {
  flex: 1;
  margin-right: 8px;
  padding: 8px;
}

button {
  padding: 8px;
}

#movieList {
  flex-wrap: wrap;
  gap: 20px;
  display: flex;
}

@media screen and (min-width: 480px) {
  div#movieList ul {
    flex-wrap: wrap;
    gap: 30px;
    margin-bottom: 50px;
    display: flex;
  }

  div#movieList li {
    width: 294px;
    margin-bottom: 0;
  }
}

